<template>
  <div class="basicInfo">
    <div class="Btitle">
      <span class="basicData">会员管理</span>
    </div>
    <div class="contact1">
      <div class="subTitle">
        会员趋势
        <span class="upperLevel"
          v-if="false">上一级</span>
        <span class="firstLevel"
          @click="getTrendRecord(2019, 1, 'api/memberTrend')">返回首级</span>
      </div>
      <div class="runVIPEcharts"
        style="width:83.8vw;height:24.3vh;">
        <IEcharts :options="abilityOverview"
          @click='onClick'
          style="width:83.8vw;height:24.3vh"></IEcharts>
      </div>
    </div>
    <div class="contact2">
      <div class="subTitle">
        <span>筛选查询</span>
        <span class="order"
          @click.stop='dropDownIconBl=!dropDownIconBl'
          v-click-outside="{fn:closIsDrop,arg:{inquire:'dropDownIconBl'}}">
          <i class="icon biFont icon-order"></i>
          <div class="dropDownIcon"
            v-show="dropDownIconBl">
            <span @click="filterClick(
					requestType,
					filterIndex,
					1,
					0
				)">
              <span class="icon biFont icon-descend"></span>
              <span>升序</span>
            </span>
            <span @click="filterClick(
					requestType,
					filterIndex,
					1,
					1
				)">
              <span class="icon biFont icon-ascend"></span>
              <span>降序</span>
            </span>
          </div>
        </span>
        <span class="break">
          <i class="icon biFont icon-reset"
            @click="pagesCallback(1, 'api/memberAdmin?page=')"></i>
        </span>
        <span class="search">
          <input type="text"
            @keyup.enter="filterClick('模糊搜索',searchVal,1,0)"
            @blur="filterClick('模糊搜索',searchVal,1,0)"
            v-model="searchVal">
          <span class="icon userCenterFont icon-search"
            @click="filterClick('模糊搜索',searchVal,1,0)"></span>
        </span>
        <span class="searchText">搜索：</span>
      </div>
      <div class="runVIPTable"
        style="width:83.8vw;height:49vh;">
        <table>
          <thead>
            <tr>
              <th class="userAppellation">用户名称</th>
              <th class="userPhone">手机号码</th>
              <th class="firmAppellation">公司名称</th>
              <th class="approveType">认证类型
                <span v-if="!dropDownObj.approveType">
                  <img src="@/assets/UserCenter/imgs/triangleDown.svg"
                    alt=""
                    @click.stop="dropDownFn('approveType')"
                    v-if="!dropDownObj.mouseleaveapproveType"
                    @mouseenter="dropDownObj.mouseleaveapproveType=true"
                    class="triangle">
                  <img src="@/assets/UserCenter/imgs/triangleDown-blue.svg"
                    alt=""
                    @click.stop="dropDownFn('approveType')"
                    v-if="dropDownObj.mouseleaveapproveType"
                    @mouseleave="dropDownObj.mouseleaveapproveType=false"
                    class="triangle">
                </span>
                <span v-if="dropDownObj.approveType">
                  <img src="@/assets/UserCenter/imgs/triangleUp.svg"
                    alt=""
                    @click.stop="dropDownObj.approveType=false"
                    v-if="!dropDownObj.mouseleaveapproveType"
                    @mouseenter="dropDownObj.mouseleaveapproveType=true"
                    class="triangle">
                  <img src="@/assets/UserCenter/imgs/triangleUp-blue.svg"
                    alt=""
                    @click.stop="dropDownObj.approveType=false"
                    v-if="dropDownObj.mouseleaveapproveType"
                    @mouseleave="dropDownObj.mouseleaveapproveType=false"
                    class="triangle">
                </span>
                <div class="dropDown"
                  v-click-outside="{fn:closIsDrop,arg:{msg:'approveType'}}"
                  v-if="dropDownObj.approveType">
                  <span v-for="(item,i) in certificationType"
                    :key="i"
                    @click="filterClick('认证类型',i,1,0)">{{item}}</span>
                </div>
              </th>
              <th class="dashboardCount">仪表盘数量</th>
              <th class="userFrom">来源
                <span v-if="!dropDownObj.userFrom&&false">
                  <img src="@/assets/UserCenter/imgs/triangleDown.svg"
                    alt=""
                    @click.stop="dropDownFn('userFrom')"
                    v-if="!dropDownObj.mouseleaveuserFrom"
                    @mouseenter="dropDownObj.mouseleaveuserFrom=true"
                    class="triangle">
                  <img src="@/assets/UserCenter/imgs/triangleDown-blue.svg"
                    alt=""
                    @click.stop="dropDownFn('userFrom')"
                    v-if="dropDownObj.mouseleaveuserFrom"
                    @mouseleave="dropDownObj.mouseleaveuserFrom=false"
                    class="triangle">
                </span>
                <span v-if="dropDownObj.userFrom">
                  <img src="@/assets/UserCenter/imgs/triangleUp.svg"
                    alt=""
                    @click.stop="dropDownObj.userFrom=false"
                    v-if="!dropDownObj.mouseleaveuserFrom"
                    @mouseenter="dropDownObj.mouseleaveuserFrom=true"
                    class="triangle">
                  <img src="@/assets/UserCenter/imgs/triangleUp-blue.svg"
                    alt=""
                    @click.stop="dropDownObj.userFrom=false"
                    v-if="dropDownObj.mouseleaveuserFrom"
                    @mouseleave="dropDownObj.mouseleaveuserFrom=false"
                    class="triangle">
                </span>
                <div class="dropDown"
                  v-click-outside="{fn:closIsDrop,arg:{msg:'userFrom'}}"
                  v-if="dropDownObj.userFrom">
                  <span v-for="(item,i) in sourceType"
                    :key="i"
                    @click="filterClick('来源',i,1,0)">{{item}}</span>
                </div>
              </th>
              <th class="spreadStyle">推广渠道
                <span v-if="!dropDownObj.spreadStyle&&false">
                  <img src="@/assets/UserCenter/imgs/triangleDown.svg"
                    alt=""
                    @click.stop="dropDownFn('spreadStyle')"
                    v-if="!dropDownObj.mouseleavespreadStyle"
                    @mouseenter="dropDownObj.mouseleavespreadStyle=true"
                    class="triangle">
                  <img src="@/assets/UserCenter/imgs/triangleDown-blue.svg"
                    alt=""
                    @click.stop="dropDownFn('spreadStyle')"
                    v-if="dropDownObj.mouseleavespreadStyle"
                    @mouseleave="dropDownObj.mouseleavespreadStyle=false"
                    class="triangle">
                </span>
                <span v-if="dropDownObj.spreadStyle">
                  <img src="@/assets/UserCenter/imgs/triangleUp.svg"
                    alt=""
                    @click.stop="dropDownObj.spreadStyle=false"
                    v-if="!dropDownObj.mouseleavespreadStyle"
                    @mouseenter="dropDownObj.mouseleavespreadStyle=true"
                    class="triangle">
                  <img src="@/assets/UserCenter/imgs/triangleUp-blue.svg"
                    alt=""
                    @click.stop="dropDownObj.spreadStyle=false"
                    v-if="dropDownObj.mouseleavespreadStyle"
                    @mouseleave="dropDownObj.mouseleavespreadStyle=false"
                    class="triangle">
                </span>
                <div class="dropDown"
                  v-click-outside="{fn:closIsDrop,arg:{msg:'spreadStyle'}}"
                  v-if="dropDownObj.spreadStyle">
                  <span v-for="(item,i) in generalizeType"
                    :key="i"
                    @click="filterClick('推广渠道',i,1,0)">{{item}}</span>
                </div>
              </th>
              <th class="VIPGrade">会员等级
                <span v-if="!dropDownObj.VIPGrade">
                  <img src="@/assets/UserCenter/imgs/triangleDown.svg"
                    alt=""
                    @click.stop="dropDownFn('VIPGrade')"
                    v-if="!dropDownObj.mouseleaveVIPGrade"
                    @mouseenter="dropDownObj.mouseleaveVIPGrade=true"
                    class="triangle">
                  <img src="@/assets/UserCenter/imgs/triangleDown-blue.svg"
                    alt=""
                    @click.stop="dropDownFn('VIPGrade')"
                    v-if="dropDownObj.mouseleaveVIPGrade"
                    @mouseleave="dropDownObj.mouseleaveVIPGrade=false"
                    class="triangle">
                </span>
                <span v-if="dropDownObj.VIPGrade">
                  <img src="@/assets/UserCenter/imgs/triangleUp.svg"
                    alt=""
                    @click.stop="dropDownObj.VIPGrade=false"
                    v-if="!dropDownObj.mouseleaveVIPGrade"
                    @mouseenter="dropDownObj.mouseleaveVIPGrade=true"
                    class="triangle">
                  <img src="@/assets/UserCenter/imgs/triangleUp-blue.svg"
                    alt=""
                    @click.stop="dropDownObj.VIPGrade=false"
                    v-if="dropDownObj.mouseleaveVIPGrade"
                    @mouseleave="dropDownObj.mouseleaveVIPGrade=false"
                    class="triangle">
                </span>
                <div class="dropDown"
                  v-click-outside="{fn:closIsDrop,arg:{msg:'VIPGrade'}}"
                  v-if="dropDownObj.VIPGrade">
                  <span v-for="(item,i) in memberType"
                    :key="i"
                    @click="filterClick('会员等级',i,1,0)">{{item}}</span>
                </div>
              </th>
              <th class="manager">管理员
                <span v-if="!dropDownObj.manager">
                  <img src="@/assets/UserCenter/imgs/triangleDown.svg"
                    alt=""
                    @click.stop="dropDownFn('manager')"
                    v-if="!dropDownObj.mouseleavemanager&&false"
                    @mouseenter="dropDownObj.mouseleavemanager=true"
                    class="triangle">
                  <img src="@/assets/UserCenter/imgs/triangleDown-blue.svg"
                    alt=""
                    @click.stop="dropDownFn('manager')"
                    v-if="dropDownObj.mouseleavemanager"
                    @mouseleave="dropDownObj.mouseleavemanager=false"
                    class="triangle">
                </span>
                <span v-if="dropDownObj.manager">
                  <img src="@/assets/UserCenter/imgs/triangleUp.svg"
                    alt=""
                    @click.stop="dropDownObj.manager=false"
                    v-if="!dropDownObj.mouseleavemanager"
                    @mouseenter="dropDownObj.mouseleavemanager=true"
                    class="triangle">
                  <img src="@/assets/UserCenter/imgs/triangleUp-blue.svg"
                    alt=""
                    @click.stop="dropDownObj.manager=false"
                    v-if="dropDownObj.mouseleavemanager"
                    @mouseleave="dropDownObj.mouseleavemanager=false"
                    class="triangle">
                </span>
                <div class="dropDown"
                  v-click-outside="{fn:closIsDrop,arg:{msg:'manager'}}"
                  v-if="dropDownObj.manager">
                  <span v-for="(item,i) in isMember"
                    :key="i"
                    @click="filterClick('管理员',i,1,0)">{{item}}</span>
                </div>
              </th>
              <th class="expireData">到期时间</th>
              <th class="modify">操作</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item,i) in tbodyArr"
              :key="i">
              <td class="userAppellation">{{item.nickname}}</td>
              <td>{{item.phone}}</td>
              <td>{{item.firm_name}}</td>
              <td>{{item.approve}}</td>
              <td>{{item.dashBoardSum}}</td>
              <td>{{item.source}}</td>
              <td>{{item.ditch}}</td>
              <td>{{item.at_present_grade}}</td>
              <td>{{item.identity}}</td>
              <td>{{item.pastdue_time}}</td>
              <td>
                <span class="icon biFont icon-edit"></span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
    <paginate :page-count="pageQuantity"
      :margin-pages="1"
      :force-page="forcePage"
      :page-range="10"
      :page-class="'page-item'"
      :page-link-class="'item'"
      :container-class="'ui pagination menu'"
      :prev-text="'<'"
      :next-text="'>'"
      :prev-link-class="'item'"
      :next-link-class="'item'"
      :break-view-class="'break-view'"
      :break-view-link-class="'break-view-link'"
      :click-handler="clickCallback"
      :no-li-surround="true">
      <span slot="breakViewContent"
        @click="jumpPage">
        <svg width="16"
          height="4"
          viewBox="0 0 16 4">
          <circle fill="#999999"
            cx="2"
            cy="2"
            r="2" />
          <circle fill="#999999"
            cx="8"
            cy="2"
            r="2" />
          <circle fill="#999999"
            cx="14"
            cy="2"
            r="2" />
        </svg>
      </span>
    </paginate>
  </div>
</template>

<script>
import IEcharts from 'vue-echarts';
import Paginate from 'vuejs-paginate';
export default {
	name: 'runVIP',
	components: { IEcharts, Paginate },
	data() {
		return {
			searchVal: '',
			generalizeType: ['无'],
			certificationType: ['无'],
			sourceType: ['无'],
			memberType: ['无'],
			isMember: ['无'],
			// 总页数
			pageQuantity: 1,
			page: 0,
			forcePage: 1,
			// 页码点击事件控制,防止频繁点击
			pageClickBl: true,
			dropDownIconBl: false,
			dropDownObj: {
				orderType: false,
				manager: false,
				mouseleavemanager: false,
				approveType: false,
				mouseleaveapproveType: false,
				userFrom: false,
				mouseleaveuserFrom: false,
				spreadStyle: false,
				mouseleavespreadStyle: false,
				VIPGrade: false,
				mouseleaveVIPGrade: false
			},
			// echarts数据表，初始化各种参数为0
			abilityOverview: {
				xAxis: {
					type: 'category',
					boundaryGap: false,
					axisLabel: {
						textStyle: {
							color: '#fff',
							fontSize: 12,
							padding: [5, 0, 0, 0]
						}
					},
					color: '#2d3540',
					data: ['0', '0', '0', '0', '0', '0', '0', '0', '0']
				},
				grid: {
					x: 70,
					y: 50,
					x2: 55,
					y2: 30
				},
				yAxis: {
					type: 'value',
					nameTextStyle: {
						color: '#fff',
						show: false,
						padding: [0, 35, 5, 0],
						fontSize: 12
					},
					axisLabel: {
						show: true,
						textStyle: {
							color: '#fff',
							fontSize: 12
						}
					}
				},
				tooltip: {
					trigger: 'axis',
					formatter: function(params) {
						return params[0].name + ':' + params[0].data + '元';
					}
				},
				series: [
					{
						data: [12, 15, 8, 7, 33, 25, 12, 35, 20],
						name: '会员趋势',
						type: 'line',
						smooth: true,
						lineStyle: {
							color: {
								type: 'linear',
								x: 0,
								y: 0,
								x2: 1,
								y2: 0,
								colorStops: [
									{
										offset: 0,
										color: 'rgba(29,124,250,1)' // 0% 处的颜色
									},
									{
										offset: 1,
										color: 'rgba(238,116,114,1)' // 100% 处的颜色
									}
								],
								globalCoord: false // 缺省为 false
							}
						},
						areaStyle: {
							color: {
								type: 'linear',
								x: 0,
								y: 0,
								x2: 1,
								y2: 0,
								colorStops: [
									{
										offset: 0,
										color: 'rgba(29,124,250,0.5)' // 0% 处的颜色
									},
									{
										offset: 1,
										color: 'rgba(238,116,114,0.5)' // 100% 处的颜色
									}
								],
								globalCoord: false // 缺省为 false
							}
						}
					}
				]
			},
			tbodyArr: [
				{
					nickname: '无',
					phone: '无',
					firm_name: '无',
					approve: '无',
					dashBoardSum: '无',
					from: '无',
					ditch: '无',
					at_present_grade: '无',
					identity: '无',
					pastdue_time: '无'
				}
			],
			requestType: '所有',
			filterIndex: 0,
			dataSort: 0
		};
	},
	methods: {
		// echarts下钻事件
		onClick(event) {
			this.echartsClick(event, 'api/memberTrend');
		},
		// echarts下钻事件分类
		echartsClick(event, url) {
			if (event.name.indexOf('年') >= 0) {
				this.getTrendRecord(event.name, 1, url);
			} else if (event.name.indexOf('月') >= 0) {
				this.getTrendRecord(event.name, 2, url);
			}
		},
		// echarts下钻请求
		getTrendRecord(time, dataType, url) {
			var parmas = {
				date: parseInt(time),
				dateType: dataType
			};
			if (dataType == '2') {
				parmas.year = '2019';
			}
			this.$post(url, parmas).then(res => {
				if (res.code == '000000') {
					if (res.data) {
						var arr = [];
						var arr1 = [];
						if (dataType == 1) {
							for (const i in res.data) {
								if (res.data[i] && i != 0) {
									arr.push(res.data[i].money);
									arr1.push(res.data[i].monthly + '月');
								}
							}
						} else if (dataType == 2) {
							for (const i in res.data) {
								if (res.data[i] && i != 0) {
									arr.push(res.data[i].money);
									arr1.push(res.data[i].days + '日');
								} else if (i != 0) {
									arr.push(0);
									arr1.push(res.data[i].days + '日');
								}
							}
						}
						this.abilityOverview.xAxis.data = arr1;
						this.abilityOverview.series[0].data = arr;
					} else {
						this.$store.dispatch('publicTools/setTipsData', [
							'年暂时没有充值数据'
						]);
					}
				}
			});
		},
		// 页码点击事件
		clickCallback(val) {
			if (this.requestType == '所有') {
				this.pagesCallback(val, 'api/memberAdmin?page=');
			} else {
				this.filterClick(
					this.requestType,
					this.filterIndex,
					val,
					this.dataSort
				);
			}
		},
		// 翻页点击事件
		jumpPage(obj) {
			if (this.pageClickBl) {
				if (obj.x < obj.y) {
					if (this.forcePage - 10 > 0) {
						this.clickCallback(this.forcePage - 10);
					} else {
						this.clickCallback(1);
					}
				} else {
					if (this.forcePage + 10 < this.pageQuantity) {
						this.clickCallback(this.forcePage + 10);
					} else {
						this.clickCallback(this.pageQuantity);
					}
				}
			} else {
				return;
			}
		},
		// 当没有筛选条件时的翻页事件
		pagesCallback(val, url) {
			if (this.pageClickBl) {
				this.searchVal = '';
				this.pageClickBl = false;
				this.forcePage = val;
				this.$get(url + val).then(res => {
					if (res.code == '000000') {
						this.requestType = '所有';
						// 赋值
						// var xAxisArr = [];
						// var series0Arr = [];
						// for (let i = 0; i < res.data.annuaMoney.length; i++) {
						// 	xAxisArr.push(res.data.annuaMoney[i].annual + '年');
						// 	series0Arr.push(res.data.annuaMoney[i].money);
						// }
						// this.abilityOverview.xAxis.data = xAxisArr;
						// this.abilityOverview.series[0].data = series0Arr;

						this.pageClickBl = true;
						this.currentPageQuantity = val;
						if (res.data.count && res.data.count != 0) {
							this.pageQuantity = res.data.count;
						} else {
							this.pageQuantity = 1;
						}
						this.tbodyArr = res.data.user_hierarchy_data;
						for (const key in this.dropDownObj) {
							this.dropDownObj[key] = false;
						}
					}
				});
			}
		},
		// 下拉框排他事件
		closIsDrop(arg) {
			if (arg.inquire) {
				this[arg.inquire] = false;
			}
			if (arg.msg) {
				this.dropDownObj[arg.msg] = false;
			}
		},
		// 下拉按钮事件，添加了防冒泡的事件，否则冒泡到下拉框排他事件
		dropDownFn(blKey) {
			for (const key in this.dropDownObj) {
				if (key == blKey) {
					this.dropDownObj[key] = true;
				} else {
					this.dropDownObj[key] = false;
				}
			}
		},
		// 下拉框的筛选按钮点击事件和筛选完毕后的翻页事件
		filterClick(val, index, page, sort) {
			// 如果用户选的是全部，直接return，初始化所有值
			// if (index == 0) {
			// 	return this.pagesCallback(1, 'api/memberAdmin?page=');
			// }
			if (sort || sort == 0) {
				this.dataSort = sort;
			}
			var data = {
				page: page,
				value: index,
				condition: val,
				sort: this.dataSort
			};
			if (index == 0) {
				data = { sort: this.dataSort, page: page };
			} else if (val != '模糊搜索') {
				data = {
					page: page,
					value: index - 1,
					condition: val,
					sort: this.dataSort
				};
			}
			this.$post('api/memberScreen', data).then(res => {
				if (res.code == '000000') {
					this.tbodyArr = res.data.user_hierarchy_data;
					if (res.data.count && res.data.count != 0) {
						this.pageQuantity = res.data.count;
					} else {
						this.pageQuantity = 1;
					}
					this.filterIndex = index;
					this.requestType = val;
					this.dataSort = sort;
					for (const key in this.dropDownObj) {
						this.dropDownObj[key] = false;
					}
				}
			});
		}
	},
	mounted() {
		// 初始化页面的值
		this.pagesCallback(1, 'api/memberAdmin?page=');
		this.getTrendRecord(2019, 1, 'api/memberTrend');
		// 初始化下拉框的值
		this.$get('api/sizerCondition').then(res => {
			if (res.code == '000000') {
				res.data.generalizeType.unshift('全部');
				res.data.certificationType.unshift('全部');
				res.data.sourceType.unshift('全部');
				res.data.memberType.unshift('全部');
				res.data.isMember.unshift('全部');
				this.generalizeType = res.data.generalizeType;
				this.certificationType = res.data.certificationType;
				this.sourceType = res.data.sourceType;
				this.memberType = res.data.memberType;
				this.isMember = res.data.isMember;
			}
		});
	}
};
</script>

<style scoped>
.basicInfo >>> .pagination {
  position: fixed;
  bottom: 2vh;
  font-size: 0.8vw;
  width: 83.8vw;
  text-align: center;
}
.basicInfo >>> .item {
  display: inline-block;
  font-size: 0.8vw;
  width: 1.6vw;
  height: 1.6vw;
  text-align: center;
  line-height: 1.6vw;
  border: solid 0.0625vw #00b9ff;
  margin-left: 0.5vw;
}
.basicInfo >>> .active {
  background-color: #3dadff;
  border: solid 0.0625vw #3dadff;
